.search-result {
    &__head {
        padding-top: 12px;
        box-shadow: 0 4px 2px rgba(#eaeaea, 0.5);
        .form-group {
            margin-right: 12px;
            margin-bottom: 12px;
        }
        .form-control:not(textarea) {
            height: 36px;
        }
    }
    &__head-btn {
        margin-bottom: 12px;
        background: transparent;
        &:focus {
            outline: none;
        }
        &.active {
            svg {
                fill: #EBA32E;
            }
        }
    }
    &__head-btn-create {
        margin-bottom: 12px;
    }
    &__head-space {
        height: 26px;
        margin-bottom: 12px;
        border-left: 1px solid #E2E2E2;
    }
    &__content,&__filter-bar {
        padding-top: 30px;
    }
    &__map {
        min-height: 100vh;
    }
}

@media (max-width: 1199px) {
    .search-result {
        &__head {
            padding-top: 24px;
            padding-bottom: 12px;
            .form-group {
                margin-right: 0;
            }
        }
        &__head-wrap {
            max-width: 399px;
        }
    }
}