.search-filter {
    .nav-link {
        padding: 12px 20px;
        font-size: 14px;
        font-weight: 700;
        color: #9B9B9B;
        background: #EEEEEE;
        &.active {
            color: #EBA32E;
            background: #fff;
            svg {
                fill: #EBA32E;
            }
        }
        &:hover {
            background: #fff;
        }
    }
    .tab-content {
        background: #fff;
    }
    .tab-pane {
        box-shadow: 0 4px 8px 0 rgba(27, 69, 167, 0.20);
    }
    &__wrap {
        padding: 20px 20px 4px;
    }
    &__btn-search {
        width: 86px;
        height: 80px;
        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12);
        font-size: 14px;
        font-weight: 700;
        color: #fff;
        background: #EBA32E;
        &:hover {
            background: rgb(230, 145, 9);
        }
    }
    &__bg-search {
        padding-right: 30px !important;
        background-image: url(../images/ic-search.svg) !important;
        background-repeat: no-repeat !important;
    }
    &__bg-date {
        @extend .search-filter__bg-search;
        background-image: url(../images/ic-date-range.svg) !important;
    }
    &__bg-location {
        @extend .search-filter__bg-search;
        background-image: url(../images/ic-location-on.svg) !important;
    }
    &__bg-date-color {
        @extend .search-filter__bg-search;
        background-image: url(../images/ic-date-range-color.svg) !important;
    }
    .form-control {
        font-size: 13px !important;
    }
}

@media (max-width: 1199px) {
    .search-filter {
        .tab-content.m-rounded-tr {
            border-radius: 0;
        }
        &__btn-search {
            width: 100%;
            height: 40px;
            border-radius: .25rem;
            svg {
                width: 22px;
                height: 22px;
            }
        }
        &__btn-wrap {
            padding: 0 20px 20px;
        }
    }
}

@media (max-width: 575px) {
    .search-filter {
        &__wrap {
            padding: 16px 8px 0;
        }
        &__btn-wrap {
            padding: 0 8px 16px;
        }
    }
}

@media (max-width: 439px) {
    .search-filter {
        .nav-link {
            padding: 8px;
            font-size: 13px;
            svg {
                width: 16px;
                height: 20px;
                margin-right: 2px;
            }
        }
    }
}