.create-custom-tour {
    border-radius: 3px;
    box-shadow: 0 1px 4px 0 rgba(0,0,0,0.10);
    &__body {
        border-bottom: 1px solid #E2E2E2;
    }
    &__content {
        max-width: 575px;
    }
    &__htext {
        font-weight: 700;
        font-size: 30px;
        color: #2C2C2C;
    }
    &__title {
        font-weight: 700;
        color: #2C2C2C;
    }
    &__label {
        min-width: 124px;
        font-weight: 500;
        font-size: 15px;
        color: #4A4A4A;
    }
    &__ic-input {
        top: 10px;
        right: 8px;
        z-index: 0;
    }
    &__input-price {
        width: 60px;
        border: none;
        border-bottom: 1px solid #E2E2E2;
        outline: none;
        font-weight: 700;
        font-size: 14px;
        color: #4A4A4A;
        &:focus {
            border-bottom: 1px solid #EBA32E;
        }
    }
    &__money-price {
        font-weight: 700;
        font-size: 14px;
        color: #4A4A4A;
    }
    .m-btn-grey {
        font-size: 15px;
    }
}
@media (min-width: 576px) {
    .create-custom-tour {
        &__label {
            &--hook {
                min-width: 32px;
            }
        }
    }
}

@media (max-width: 575px) {
    .create-custom-tour {
        &__label {
            &--date {
                min-width: 60px;
            }
        }
    }
}