.filter-bar {
    margin-bottom: 20px;
    border-radius: 3px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12);
    background: #FFFFFF;
    &__head {
        padding: 12px 20px;
        border-bottom: 1px solid #eaeaea;
        font-size: 15px;
        font-weight: 700;
        background: #FCFCFC;
    }
    &__body {
        padding: 20px;
    }
    &__item {
        margin-bottom: 20px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &__item-checkbox:checked+.filter-bar__item-status {
        border-color: #EBA32E;
        background-color: #EBA32E;
        background-image: url(../images/ic-checkbox.svg);
        background-size: contain;
        background-position: center center;
    }
    &__item-status {
        width: 24px;
        height: 24px;
        border: 2px solid #E2E2E2;
        border-radius: 2px;
    }
    &__text1 {
        font-size: 13px;
        color: #888888;
    }
    &__text2 {
        font-size: 13px;
    }
    &__text3 {
        font-size: 13px;
        font-weight: 700;
        color: #000;
    }
    &__text4 {
        font-size: 13px;
        text-decoration: underline;
        color: #000;
    }
}