.book {
    &__wrap {
        @extend .create-custom-tour;
    }
    &__wrap1 {
        @extend .book__text-7;
        background: #F6F6F6;
        border: 1px solid #E2E2E2;
        border-radius: 3px;
    }
    &__wrap2 {
        background: #FFFFFF;
        border: 1px solid #E2E2E2;
        border-radius: 3px;
        padding: 24px 20px;
    }
    &__content {
        max-width: 970px;
    }
    &__step-line {
        top: 17px;
        height: 2px;
        background: #D8D8D8;
        background-clip: content-box;
    }
    &__step-item {
        // min-width: 100px;
    }
    &__step-item-title {
        top: -27px;
        left: -33px;
        width: 100px;
        font-weight: 500;
        font-size: 13px;
        color: #888888;
    }
    &__step-item-circle {
        width: 35px;
        height: 35px;
        background: #D8D8D8;
    }
    .m-done {
        .book__step-item-title {
            color: #2C2C2C;
        }
        .book__step-item-circle {
            background-color: #E61F26;
            background-image: url(../images/ic-checkbox.svg);
            background-size: 30px 30px;
            background-repeat: no-repeat;
            background-position: center center;
        }
    }
    .m-active {
        .book__step-item-title {
            font-weight: 700;
            color: #E61F26;
        }
        .book__step-item-circle {
            background-color: #fff;
            border: 2px solid #E61F26;
        }
    }
    &__line {
        height: 5px;
        background: #f8f8f8;
    }
    &__line1 {
        height: 1px;
        background: #F8F8F8;
    }
    &__title {
        font-weight: 700;
        font-size: 30px;
        color: #2C2C2C;
        &:hover {
            color: #000;
        }
    }
    &__title2 {
        font-weight: 700;
        font-size: 20px;
        color: #2C2C2C;
    }
    &__text-1 {
        font-weight: 500;
        font-size: 15px;
        color: #2C2C2C;
    }
    &__text-2 {
        font-size: 15px;
        color: #4A4A4A;
    }
    &__text-3 {
        font-weight: 700;
        font-size: 24px;
        color: #E61F26;
    }
    &__text-4 {
        font-weight: 500;
        font-size: 22px;
        text-decoration: underline;
        color: #4A4A4A;
    }
    &__text-5 {
        font-weight: 500;
        font-size: 13px;
        color: #4A4A4A;
    }
    &__text-6 {
        font-weight: 500;
        font-size: 13px;
        color: #E61F26;
    }
    &__text-7 {
        font-size: 13px;
        color: #4A4A4A;
    }
    &__text-8 {
        font-weight: 500;
        font-size: 13px;
        text-decoration: underline;
        color: #4A4A4A;
    }
    &__text-9 {
        font-size: 13px;
        color: #2C2C2C;
        font-weight: 400;
    }
    &__text-10 {
        font-size: 13px;
        color: #000000;
        font-weight: 400;
    }
    &__text-11 {
        font-size: 13px;
        color: #2C2C2C;
        font-weight: 700;
    }
    &__text-12 {
        font-weight: 500;
        font-size: 20px;
        color: #4A4A4A;
    }
    &__text-13 {
        font-weight: 700;
        font-size: 30px;
        color: #E61F26;
    }
    &__text-14 {
        font-weight: 400;
        font-size: 30px;
        color: #000000;
        text-decoration: underline;
    }
    &__text-15 {
        font-weight: 500;
        font-size: 13px;
        color: #2C2C2C;
    }
    &__text-16 {
        font-weight: 700;
        font-size: 15px;
        color: #2C2C2C;
    }
    &__text-17 {
        font-weight: 400;
        font-size: 15px;
        color: #000000;
        text-decoration: underline;
    }
    &__text-18 {
        font-weight: 400;
        font-size: 15px;
        color: #2C2C2C;
    }
    &__text-19 {
        font-weight: 700;
        font-size: 13px;
        color: #E61F26;
    }
    &__text-20 {
        font-size: 13px;
        color: #2C2C2C;
        font-weight: 500;
    }
    &__link1 {
        font-weight: 500;
        font-size: 13px;
        color: #2D94F4;
        text-decoration: underline;
    }
    &__link2{
        font-weight: 700;
        font-size: 15px;
        color: #EBA32E;
        text-decoration: none;
    }
    &__link2:hover{
        color: rgb(169, 118, 36);
        text-decoration: none;
    }
    &__customer-list {
        border: 1px solid #E2E2E2;
        border-radius: 3px;
    }
    &__customer-list-title {
        border-bottom: 1px solid #E2E2E2;
        font-weight: 700;
        font-size: 15px;
        color: #E61F26;
        background: #F8F8F8;
    }
    &__customer-list-content {
        min-width: 936px;
    }
    &__customer-list-item {
        border-bottom: 1px solid #E2E2E2;
    }
    &__customer-list-item-w120 {
        width: 120px;
        background-position: 90px center !important;
    }
    &__customer-list-item-w224 {
        width: 224px;
    }
    &__customer-list-item-w214 {
        width: 214px;
        background-position: 184px center !important;
    }
    &__item {
        margin-bottom: 20px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &__item-checkbox:checked+.create-custom-tour__item-status {
        border-color: #E61F26;
        background-color: #E61F26;
        background-image: url(../images/ic-checkbox.svg);
        background-size: contain;
        background-position: center center;
    }
    &__item-status {
        width: 24px;
        height: 24px;
        border: 2px solid #E2E2E2;
        border-radius: 2px;
    }
    &__service {
        .nav-tabs .nav-item a {
            font-weight: 400;
            font-size: 15px;
            color: #2C2C2C;
        }
        .nav-tabs .nav-item a.active {
            font-weight: 700;
            font-size: 15px;
            color: #E61F26;
            background-color: #f7f7f7;
            border-bottom: 1px solid #f7f7f7;
        }
        .tab-content {
            border: 1px solid #E2E2E2;
            border-top: none;
            background-color: #f7f7f7;
        }
        table {
            overflow-x: scroll;
            background-color: #ffffff;
        }
        th,
        td {
            padding-left: 20px;
        }
        th {
            padding-top: 15px;
            background-color: #fcfcfc;
        }
    }
    &__radio {
        display: block;
        position: relative;
        padding-left: 34px;
        cursor: pointer;
        user-select: none;
        input {
            position: absolute;
            opacity: 0;
            cursor: pointer;
        }
        input:checked~.book__radio-status {
            background-color: #ffffff;
            border: 2px solid #E2E2E2;
        }
        &-status {
            position: absolute;
            margin-top: 1px;
            left: 0;
            height: 20px;
            width: 20px;
            background-color: #ffffff;
            border: 2px solid #E2E2E2;
            border-radius: 50%;
        }
        &-status:after {
            content: "";
            position: absolute;
            display: none;
        }
    }
    &__radio input:checked~.book__radio-status:after {
        display: block;
    }
    &__radio .book__radio-status:after {
        top: 2px;
        left: 2px;
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background: #E61F26;
    }
    &__price {
        .d-flex {
            border: 1px solid #E2E2E2;
        }
    }
}

@media (max-width: 1199px) {
    .book {
        &__customer-list {
            overflow-x: scroll;
        }
        &__service {
            .tab-content {
                overflow-x: scroll;
            }
        }
    }
}