.m-btn {
    padding: 8px 20px;
    border-radius: 3px;
    font-family: Roboto;
    font-size: 15px;
    font-weight: 700;
    &:hover {
        text-decoration: none;
    }
}

.m-btn-orange {
    border: none;
    color: #FFFFFF;
    background: #EBA32E;
    &:hover {
        color: #FFFFFF;
        background: rgb(230, 145, 9);
    }
}

.m-btn-white {
    border: 1px solid rgba(#E2E2E2, 0.5);
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12);
    color: #FFFFFF;
    background: transparent;
    &:hover {
        color: #FFFFFF;
        border-color: #fff;
    }
}

.m-btn-grey {
    font-size: 13px;
    border: 1px solid #E2E2E2;
    color: #888888;
    background: transparent;
    &:hover {
        border-color: #EBA32E;
        color: #EBA32E;
    }
}

.m-btn-close {
    border: 1px solid #E2E2E2;
    border-radius: 3px;
    color: #888888;
    background: #F8F8F8;
    &:hover {
        border-color: #888888;
        color: #696868;
    }
}

.m-line-light-grey {
    display: block;
    width: 100%;
    border-top: 1px solid #393E4A;
}

.line-ligth-blue {
    display: block;
    width: 100%;
    height: 1px;
    background-image: linear-gradient(-90deg, rgba(#4a4a4a, 0) 0%, rgba(#3a99cc, 0.5) 50%, rgba(#4a4a4a, 0) 100%);
}

.m-line-light-grey-left {
    display: block;
    width: 100%;
    height: 1px;
    background-image: linear-gradient(90deg, rgba(#4a4a4a, 0.01) 0%, rgba(#4a4a4a, 0.2) 100%);
}

.m-line-light-grey-right {
    display: block;
    width: 100%;
    height: 1px;
    background-image: linear-gradient(-90deg, rgba(#4a4a4a, 0.01) 0%, rgba(#4a4a4a, 0.2) 100%);
}

.m-rounded-tr {
    border-top-right-radius: .25rem;
}

.m-rounded-tl {
    border-top-left-radius: .25rem;
}

.m-pd-tb {
    padding-top: 80px;
    padding-bottom: 60px;
}

.m-pd-tb1 {
    padding-top: 60px;
    padding-bottom: 60px;
}

.m-mask-grey {
    background: rgba(#000000, 0.5);
}

.m-bg-grey {
    background: #F8F8F8;
}

.m-z-outer {
    overflow: hidden;
    &:hover {
        .m-z-inner {
            transform: scale(1.25)
        }
    }
}

.m-z-inner {
    transition: 0.5s;
}

.m-title {
    font-size: 30px;
    font-weight: 700;
}

.m-text-rm {
    font-size: 15px;
    font-weight: 700;
    color: #EBA32E;
    text-decoration: underline;
    &:hover {
        text-decoration: none;
        color: rgb(230, 145, 9);
    }
}

.line-height-1-2 {
    line-height: 1.2;
}

.m-height-screen {
    min-height: calc(100vh - 130px);
}

.m-lock-y {
    overflow-y: hidden;
}

.m-row-2 {
    margin-right: -8px;
    margin-left: -8px;
}

.m-check-radio-red {
    display: flex;
    align-items: center;
    margin-bottom: 0;
    &__input {
        display: none;
    }
    &__input:checked+.m-check-radio-red__status {
        background-clip: content-box;
        background-color: #E61F26;
    }
    &__status {
        display: inline-block;
        width: 20px;
        height: 20px;
        margin-right: 10px;
        padding: 2px;
        border: 2px solid #E2E2E2;
        border-radius: 40px;
        background: #FFFFFF;
    }
    &__text {
        font-size: 15px;
    }
}

.m-check-box-red {
    display: flex;
    align-items: center;
    margin-bottom: 0;
    &__input {
        display: none;
    }
    &__input:checked+.m-check-box-red__status {
        border-color: #E61F26;
        background-color: #E61F26;
        background-image: url(../images/ic-checkbox.svg);
        background-size: contain;
        background-position: center center;
    }
    &__status {
        display: inline-block;
        width: 24px;
        height: 24px;
        margin-right: 10px;
        border: 2px solid #E2E2E2;
        border-radius: 2px;
        background: #FFFFFF;
    }
    &__text {
        font-size: 13px;
    }
}