.header {
    border-bottom: 1px solid #eaeaea;
    &__user-btn {
        max-width: 130px;
        font-size: 14px;
        background: transparent;
        &:focus {
            outline: none;
        }
    }
    &__avata {
        width: 36px;
        height: 36px;
    }
}

.header-head {
    background: #282C35;
    &__title {
        font-size: 13px;
        font-weight: 400;
        color: #ADB0B6;
    }
}

.header-menu {
    &__wrap {
        min-height: 90px;
    }
    &__logo {
        min-width: 150px;
        max-height: 55px;
    }
    &__link {
        padding: 4px 12px;
        font-size: 15px;
        font-weight: 700;
        color: #4A4A4A;
        &:hover {
            text-decoration: none;
            color: #EBA32E;
        }
    }
    &__link1 {
        padding: 4px 12px;
        font-size: 13px;
        color: #888888;
        padding: 20px 10px 17px 10px;
        
        border-bottom: 3px solid white;
        &:hover {
            text-decoration: none;
            color:#E61F26;
            border-bottom: 3px solid #E61F26;
        }
    }
    &__link1:first-child{
        padding: 20px 10px 17px 20px;
    }
    &__btn {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 105px;
        height: 36px;
        padding: 0;
        font-size: 13px;
    }
    .dropdown-item {
        font-size: 15px;
    }
}

.header-mobile {
    position: relative;
    height: 54px;
    &__fixed {
        position: fixed;
        z-index: 999;
        top: 0;
        left: 0;
        border-bottom: 1px solid #eaeaea;
        box-shadow: 0 2px 1px rgba(44, 44, 44, 0.1);
    }
    &__logo {
        width: 100px;
    }
}

.header-mobile-menu {
    visibility: hidden;
    position: fixed;
    z-index: 99;
    width: 100%;
    height: 100vh;
    padding-top: 55px;
    background: white;
    opacity: 0;
    transition: all .5s ease;
    transform: translateX(-100%);
    &.active {
        visibility: visible;
        opacity: 1;
        transform: translateX(0);
    }
    &__limit-height {
        height: calc(100vh - 55px);
        overflow-y: auto;
    }
    &__wrap {
        border-bottom: 1px solid #eaeaea;
    }
    .dropdown-item {
        font-size: 15px;
    }
}