body {
    font-family: Roboto;
    font-weight: 400;
    color: #2C2C2C;
}

hr{
    margin: 0px;
}

button {
    cursor: pointer;
    transition: all 0.3s;
}

a {
    transition: all 0.3s;
}

svg {
    transition: all 0.3s;
    path {
        transition: all 0.3s;
    }
}

textarea {
    border: none;
    overflow: auto;
    outline: none;
}

