.m-navigate {
    &__link {
        width: 40px;
        height: 40px;
        margin: 4px 7px;
        background: #F8F8F8;
        border: 1px solid #D5D5D5;
        color: #888888;
        &:hover {
            text-decoration: none;
            border-color: #EBA32E;
            color: #EBA32E;
            svg {
                path {
                    fill: #EBA32E;
                }
            }
        }
        &.active {
            border-color: #EBA32E;
            color: #fff;
            background: #EBA32E;
            svg {
                path {
                    fill: #EBA32E;
                }
            }
        }
    }
}

@media (max-width:575px) {
    .m-navigate {
        &__link {
            width:30px;
            height: 30px;
            margin: 3px;
        }
    }
}