.dropdown-menu {
    border: none; // border-radius: 0;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}

.dropdown-item {
    font-size: 13px;
    color: #4a4a4a;
}

.ui.search>.results .result .description {
    display: none;
}

.ui.search>.results .result .title,
.ui.search>.results>.message .description {
    font-family: Roboto;
    font-size: 13px;
    font-weight: 500;
    color: #495057;
}

.form-control:not(textarea) {
    height: 40px !important;
}

.form-control {
    font-size: 14px;
    color: #495057 !important;
    &::placeholder {
        color: rgba(191, 191, 191, .87) !important;
    }
    &:focus {
        box-shadow: none;
        outline: none;
        border-color: rgba(#EBA32E, 0.6) !important;
    }
}

.m-adapter {
    .ui.dropdown {
        width: 200px;
        padding-right: 10px;
        padding-left: 14px;
    }
    .ui .text {
        font-size: 13px;
        color: #DFDFDF;
    }
    .ui .text-1 {
        font-weight: 500;
    }
    .ui .text-2 {
        color: #ADB0B6;
    }
    .ui img {
        width: 24px !important;
    }
    .ui .menu {
        min-width: 100%;
    }
    .ui .item {
        font-size: 14px !important;
    }
    .ui .active {
        font-weight: 500 !important;
    }
    &--header {
        .ui.dropdown {
            height: 40px;
            border-right: 1px solid #393E4A;
            border-left: 1px solid #393E4A;
        }
    }
    &--footer {
        .ui.dropdown {
            height: 36px;
            border: 1px solid #393E4A;
            border-radius: 3px;
        }
    }
    &--menu-mobile {
        .ui.dropdown {
            width: 100%;
            height: 40px;
            border: 1px solid #eaeaea;
            border-radius: 3px;
        }
        .ui .text {
            color: #2C2C2C;
        }
    }
}

.m-selection {
    &.ui {
        .text {
            font-size: 13px;
            color: #495057;
            span {
                color: #888;
            }
        }
        .menu {
            min-width: 100%;
        }
        .item {
            font-size: 13px !important;
            span {
                color: #888;
                ;
            }
        }
        .active {
            font-weight: 500 !important;
        }
    }
    &__w-full{
        width: 100%;
        background-position: 97% center !important;
    }
    &__w85 {
        width: 85px;
        background-position: 90px center !important;
    }
    &__w120 {
        width: 120px;
        background-position: 90px center !important;
    }
    &__w150 {
        width: 150px;
        background-position: 120px center !important;
    }
    &__w180 {
        width: 180px;
        background-position: 150px center !important;
    }
    &__w200 {
        width: 200px;
        background-position: 160px center !important;
    }
    &__w235 {
        width: 235px;
        background-position: 205px center !important;
    }
    &__w300 {
        width: 300px;
        background-position: 270px center !important;
    }
}

.owl-dots {
    display: none;
}

.owl-prev,
.owl-next {
    margin: 0 !important;
    padding: 0 !important;
    background: transparent !important;
}

.m-no-ui-slider-pick-price {
    .noUi-horizontal {
        height: 3px;
    }
    .noUi-target {
        border: none;
        border-radius: 10px;
        background: #E2E2E2;
    }
    .noUi-connect {
        background: #EBA32E;
    }
    .noUi-horizontal .noUi-handle {
        top: -9px;
        width: 20px;
        height: 20px;
        &:focus {
            outline: none;
        }
    }
    .noUi-handle {
        border-radius: 50%;
    }
    .noUi-handle:after,
    .noUi-handle:before {
        display: none;
    }
}

@media (min-width: 1200px) {
    .m-row-xl-8 {
        margin-right: -8px;
        margin-left: -8px;
    }
}

@media (min-width: 999px) {
    .owl-nav {
        margin-top: 0 !important;
    }
    .owl-prev,
    .owl-next {
        position: absolute;
        top: 50%;
        margin-top: -25px !important;
    }
    .owl-prev {
        left: -25px;
    }
    .owl-next {
        right: -25px;
    }
    .carousel-control-next,
    .carousel-control-prev {
        width: 0;
    }
}

@media (max-width: 1199px) {
    .m-selection {
        &__w85,
        &__w120,
        &__w150,
        &__w180,
        &__w200,
        &__w235,
        &__w300 {
            width: 100%;
            background-position: 97% center !important;
        }
    }
}

@media (max-width: 991px) and (min-width: 576px) {
    .carousel-control-next,
    .carousel-control-prev {
        width: 0;
    }
}