.manage-bar {
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.10);
    border-radius: 3px;
    &__avata {
        width: 140px;
        height: 140px;
    }
    &__avata-file {
        right: 50%;
        bottom: 0;
        width: 32px;
        height: 32px;
        margin-right: -16px;
        margin-bottom: -16px;
        box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.10);
        background: #FFFFFF;
        cursor: pointer;
    }
    &__username {
        font-weight: 700;
        font-size: 15px;
        color: #4A4A4A;
    }
    &__body {
        list-style: none;
    }
    &__link {
        display: block;
        padding: 18px 30px;
        border-top: 1px solid #F0F0F0;
        font-size: 13px;
        color: #666666;
        &:hover {
            text-decoration: none;
            color: #E61F26;
        }
    }
    .m-active {
        font-weight: 700;
        color: #E61F26;
        path {
            fill: #E61F26;
        }
    }
}