.hl-car {
    padding-top: 36px;
    padding-bottom: 60px;
}

.hl-item {
    &__badge {
        top: 24px;
        left: -4px;
    }
    &__star {
        width: 15px !important;
    }
    &__wrap1 {
        position: relative;
        box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.10);
        border-radius: 3px;
    }
    &__wrap2 {
        @extend .hl-item__wrap1;
        margin-bottom: 20px;
    }
    &__content1 {
        padding: 20px;
    }
    &__content2 {
        position: absolute;
        width: 100%;
        bottom: 0;
        left: 0;
        padding: 50px 20px 20px;
        background-image: linear-gradient(-180deg, rgba(0, 0, 0, 0.00) 0%, #000000 100%);
    }
    &__content3 {
        padding: 35px 20px;
    }
    &__content4 {
        padding: 30px 20px;
    }
    &__title1 {
        font-size: 15px;
        line-height: 1.3;
        font-weight: 700;
        color: #2C2C2C;
    }
    &__title2 {
        @extend .hl-item__title1;
        font-size: 14px;
    }
    &__title3 {
        font-size: 20px;
        font-weight: 700;
        color: #2C2C2C;
    }
    &__title4 {
        font-size: 30px;
        font-weight: 700;
        color: #2C2C2C;
    }
    &__title5 {
        font-weight: 700;
        font-size: 18px;
        color: #2C2C2C;
    }
    &__label1 {
        font-size: 13px;
        font-weight: 500;
    }
    &__label2 {
        font-size: 15px;
        font-weight: 500;
    }
    &__text1 {
        font-size: 13px;
        font-weight: 400;
        color: #4A4A4A;
    }
    &__text2 {
        font-size: 20px;
        font-weight: 700;
        color: #E61F26;
    }
    &__text3 {
        font-size: 20px;
        font-weight: 700;
        text-decoration: underline;
    }
    &__text4 {
        font-size: 13px;
        font-weight: 700;
        text-decoration: line-through;
        color: #888888;
    }
    &__text5 {
        font-size: 15px;
        font-weight: 500;
        color: #4A4A4A;
    }
    &__text6 {
        font-size: 13px;
        font-weight: 500;
        color: #888888;
    }
    &__text7 {
        font-size: 24px;
        font-weight: 700;
        color: #888888;
    }
    &__text8 {
        font-size: 15px;
        color: #4A4A4A;
        line-height: 26px;
    }
    &__text9 {
        font-size: 14px;
        color: #9B9B9B;
        line-height: 22px;
    }
    &__text10 {
        font-size: 20px;
        color: #4A4A4A;
    }
    &__text11 {
        font-size: 13px;
        color: #4A4A4A;
        line-height: 20px;
        font-weight: 400;
    }
    &__text12 {
        font-size: 13px;
        font-weight: 500;
        color: #4A4A4A;
    }
    &__link1 {
        font-size: 13px;
        font-weight: 500;
        text-decoration: underline;
        &:hover {
            text-decoration: none;
        }
    }
    &__link2 {
        font-size: 13px;
        font-weight: 500;
        color: #EBA32E;
        &:hover {
            text-decoration: none;
            color: rgb(230, 145, 9);
        }
    }
    &__link3 {
        @extend .hl-item__link2;
        font-size: 15px;
    }
    &__link4 {
        @extend .hl-item__link1;
        font-size: 15px;
    }
    &__link5 {
        @extend .hl-item__link1;
        text-decoration: none;
    }
    &__bdr1 {
        border-right: 1px solid #E2E2E2;
    }
    &__bdl1 {
        border-left: 1px solid #E2E2E2;
    }
    &__badge1 {
        font-size: 18px;
        padding: 12px;
    }
    &__badge2 {
        font-size: 16px;
        padding: 8px;
    }
}

@media (min-width: 1200px) {
    .hl-item {
        &__col-xl-4-plus {
            flex: 0 0 35.333333%;
            max-width: 35.333333%;
        }
        &__col-xl-4-minus {
            flex: 0 0 31.333333%;
            max-width: 31.333333%;
        }
    }
}

@media (min-width: 1300px) {
    .hl-item {
        &__col-xl-4-plus {
            flex: 0 0 38.333333%;
            max-width: 38.333333%;
        }
        &__col-xl-4-minus {
            flex: 0 0 28.333333%;
            max-width: 28.333333%;
        }
    }
}

@media (max-width: 767px) {
    .hl-item {
        &__bdr1 {
            border-right: none;
        }
        &__bdl1 {
            border-left: none;
        }
        &__content1-pt-0 {
            padding-top: 0;
        }
    }
}