.footer {
    background: #282C35;
    &__title {
        margin-bottom: 12px;
        font-size: 15px;
        font-weight: 700;
        color: #DFDFDF;
    }
    &__link {
        margin-bottom: 6px;
        font-size: 13px;
        color: #ADB0B6;
        &:hover {
            color: #fff;
        }
    }
    &__logo {
        max-width: 122px;
        max-height: 44px;
    }
    &__license {
        font-size: 13px;
        font-weight: 400;
        color: rgba(#fff, 0.5);
    }
}