.manage {
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.10);
    border-radius: 3px;
    &__head {
        border-bottom: 4px solid #F8F8F8;
        font-weight: 700;
        font-size: 30px;
    }
    &__head-sub {
        font-weight: 700;
        font-size: 20px;
        a {
            color: #2C2C2C;
            &:hover {
                text-decoration: none;
                color: #2D94F4;
                path {
                    fill: #2D94F4;
                }
            }
        }
        svg {
            vertical-align: -2px;
        }
    }
    &__body {
        border-bottom: 4px solid #F8F8F8;
    }
    &__foot {}
    &__label {
        @extend .create-custom-tour__label;
    }
    &__wi {
        max-width: 400px;
    }
    &__select {
        -moz-appearance: none;
        -webkit-appearance: none;
    }
    &__chevron-down {
        top: 12px;
        right: 10px;
    }
    &__line {
        border-bottom: 1px solid #F0F0F0;
    }
    &__title {
        font-weight: 700;
        font-style: 15px;
    }
    &__code {
        font-weight: 500;
        color: #2D94F4;
        &:hover {
            color: #2D94F4;
        }
    }
    &__price {
        font-weight: 700;
        color: #E61F26;
    }
    &__unit {
        text-decoration: underline;
        color: #000000;
    }
    &__tb-content {
        width: 368px;
    }
    .table {
        width: 825px;
    }
    .table td,
    .table th {
        padding-top: 18px;
        padding-bottom: 18px;
        font-size: 15px;
        color: #4A4A4A;
        &:first-child {
            padding-left: 30px;
        }
    }
    &__text-1 {
        font-weight: 500;
        font-size: 15px;
    }
    &__text-2 {
        font-weight: 400;
        font-size: 15px;
        color: #4A4A4A;
    }
    &__text-3 {
        font-weight: 500;
        font-size: 13px;
    }
    &__text-4 {
        font-weight: 400;
        font-size: 13px;
        color: #4A4A4A;
        a {
            font-weight: 500;
            color: #2D94F4;
        }
    }
    &__box-info {
        border: 1px solid #E2E2E2;
        border-radius: 3px;
    }
}