.banner-bg {
    &__mask1 {
        min-height: 600px;
        padding-top: 120px;
        padding-bottom: 100px;
    }
    &__mask2 {
        min-height: 390px;
        padding-top: 130px;
        padding-bottom: 110px;
    }
    &__content2 {
        max-width: 440px;
    }
    &__text1 {
        font-size: 13px;
        font-weight: 400;
        color: #FFFFFF;
    }
    &__text2 {
        font-size: 15px;
        color: #FFFFFF;
    }
}

@media (min-width: 1200px) {
    .banner-bg {
        &__content1 {
            width: 80%;
        }
    }
}

@media (max-width: 767px) {
    .banner-bg {
        &__mask1 {
            padding-top: 60px;
            padding-bottom: 40px;
        }
    }
}