.modal-lrf,.modal-c,.modal-s {
    .modal-title {
        font-size: 15px;
        font-weight: 700;
    }
    .close {
        font-size: 20px;
        &:focus {
            outline: none;
        }
    }
    &__call {
        font-size: 13px;
        font-weight: 700;
        text-decoration: underline;
        color: #0077cc;
    }
    &__text {
        font-size: 12px;
    }
    &__fb {
        border-color: #3a559f;
        background: #3a559f;
        &:hover {
            border-color: #3a559f;
            color: #3a559f;
            background: #fff;
            svg path {
                fill: #3a559f;
            }
        }
    }
    &__gg {
        border-color: #d52d0b;
        background: #d52d0b;
        &:hover {
            border-color: #d52d0b;
            color: #d52d0b;
            background: #fff;
            svg path {
                fill: #d52d0b;
            }
        }
    }
}

@media (min-width: 576px) {
    .modal-lrf {
        .modal-dialog {
            max-width: 400px;
        }
    }
    .modal-c{
        .modal-dialog {
            max-width: 743px;
            margin-top: 10%;
        }
    }
    .modal-s{
        .modal-dialog {
            max-width: 743px;
            margin-top: 5%;
        }
    }
}