.button-back-top {
    display: none;
    position: fixed;
    z-index: 999;
    right: 25px;
    bottom: 75px;
    img {
        transform: rotate(-90deg);
        width: 50px;
        height: 50px;
    }
}

@media (max-width: 575px) {
    .button-back-top {
        right: 10px;
        img {
            width: 40px;
            height: 40px;
        }
    }
}