.m-breadcrumb {
    &__list {
        list-style: none;
    }
    &__item {
        font-weight: 500;
        font-size: 12px;
        line-height: 1;
        color: #888888;
        a {
            color: #4a4a4a;
            &:hover {
                color: #E32327;
            }
        }
    }
}